<script>
import ToggleAbility from '@/components/Combat/ToggleAbility';

export default {
	name: 'RollResult',
	components: {
		ToggleAbility
	},
	props: {
		rollResults: {
			type: Object,
			default: () => {},
		},
	},
	data() {
		return {
			checked: false,
		}
	},
	computed: {
		isVisible() {
			return Boolean(this.rollResults);
		},
	},
	methods: {
		rerollDice() {
			this.$emit('reroll');
		},
		closeDialog() {
			this.$emit('close-dialog');
		},
		rollCrit(crit) {
			this.$emit('roll-crit', crit);
		}
	},
};
</script>

<template>
	<el-dialog class="roll__wrapper"
						 :visible="isVisible"
						 title="Результат броска"
						 width="30%"
						 @close="closeDialog">
		<span  class="roll__total"
					 :class="{'crit' : rollResults.type === 'crit'}">
			{{ rollResults.value }}
		</span>

		<span class="roll__details"
					v-html="rollResults.formula"/>

		<span slot="footer"
					class="dialog-footer">
		<el-checkbox v-if="rollResults.kind === 'damage'" v-model="checked" @change="rollCrit">isCrit?</el-checkbox>

			<div class="buttons">
					<el-button @click="rerollDice" size="small">Reroll</el-button>

					<el-button type="primary"
										 size="small"
										@click="closeDialog">Close</el-button>
			</div>
  </span>
	</el-dialog>
</template>

